.headingcolor1{
  color: #64FFDA;
  font-weight: 300;
}
.headingcolor2{
  color: #CCD6F6;
}
.headingcolor3{
  color: #8892B0;
}
.headingcolor4{
  color: #838DAA;
  font-weight: 400;
}
.headingSize1{
    font-size: 60px;
    font-weight: 700;
}
.headerDesc{
  width: 50%;
}
.profile_pic{
  vertical-align: middle;
  width: 300px;
  height: 300px;
  border-radius: 3%;
}
.getInTouch{
  background-color: rgb(10, 25, 47);
  font-size: large;
  padding: 1% 3% 1% 3%;
  color: #64FFDA;
  border:1px solid #64FFDA;
  border-radius: 10px;
  margin-top: 2%;
}

.hide_hr{
  color: rgb(10, 25, 47);
}

.headingContainer{
  margin-top: 6%;
  margin-bottom: 20%;
}

.aboutmecontainer{
  margin-bottom: 12%;
}

.desctag{
  font-weight: 500;
}

.education{
  margin-top: 10%;
}

.edCard{
  margin-top: 3%;
  background-color: #172A45;
  /* border: 1px solid #64FFDA; */
  height: 33vh;
  display: flex;
  justify-content: space-around;
}
.eduCategory{
  padding-top: 5%;
  font-weight: 500;
  font-size: large;
}
.edCard:last-child{
  padding-bottom: 3%;
}
.experienceLink{
  color: #8892B0;
  font-size: large;
}

.experienceLink:hover{
  color: #64FFDA;
  border-left: 1px solid #64FFDA;
  cursor: pointer;
}

.experienceCard{
  background-color: #172A45;
  /* background-color: rgb(10, 25, 47); */
  margin-top: 3%;
  height: max-content;
  width: 80%;
  display: flex;
  border: none;
}

.experienceHeading{
  font-weight: 500;
  font-size: x-large;
  padding: 2% 2% 1% 2%;
}

.bullet{
  color: #64FFDA;
  padding: 1%;
}

.experienceDate{
  padding-left: 2%;
  padding-bottom: 1%;
}

.projectContainer{
  margin-top: 10%;
  margin-bottom: 10%;
}

.projectsHolder{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding-left: 5%;
  padding-right: 5%;
  grid-gap: 1%;
}

@media only screen and (max-width: 600px) {
  .projectsHolder{
    grid-template-columns: 1fr;
    margin-bottom: 10%;
  }
}

@media only screen and (max-width: 600px) {
  .headingSize1{
    font-size: 40px;
    margin-bottom: 5%;
  }
  .getInTouch{
    margin-top: 5%;
    width: 50%;
    height: 15%;
  }
}


@media only screen and (max-width: 1000px) and (min-width: 600px) {
  .projectsHolder{
    grid-template-columns: 1fr 1fr;
    margin-bottom: 10%;
  }
}

.projectcard{
  background-color: #172A45;
}

.projectcard_firstrow{
  display: flex;
  margin: 8% 5% 5% 8%;
  justify-content: space-between;
}

.projectheading{
  margin-left: 8%;
}

.projectdescription{
  margin: 8%;
  color: #CCD6F6;
}

.projecttechnologies{
  margin-left: 8%;
  margin-right: 8%;
  margin-bottom: 6%;
}

.technologyheader{
  margin-top: 10%;
}

.techcard{
  background-color: rgb(10, 25, 47);
  border: none;
  font-size: large;
}

.lastsection{
    margin-top: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.last_heading{
  font-weight: 700;
  color: #CCD6F6;
  font-size: xx-large;
  margin: 2%;
}

.last_desc{
  font-weight: 600;
  font-size: large;
  width: 50%;
  margin-bottom: 3%;
}

.last_button{
  background-color: rgb(10, 25, 47);
  font-size: large;
  padding: 1% 3% 1% 3%;
  color: #64FFDA;
  border:1px solid #64FFDA;
  border-radius: 10px;
  margin-bottom: 10%;
}

.email{
  font-weight: 300;
  font-size: x-large;
  margin-top: 4%;
}

.footer_section{
  margin-bottom: 3%;
}

.social_icon:hover{
  color: #64FFDA;
}

a{
  text-decoration: none;
  color: #CCD6F6;
}

.archieveproject{
  font-weight: 500;
  margin-bottom: 2%;
}

.archieveproject:hover{
    cursor: pointer;
}



.navcontainer{
  margin-top: 2%;
}

.navtext{
  padding-left: 2%;
  padding-right: 2%;
  color: #CCD6F6;
  font-size: large;
}

.navtext:hover{
  color: #64FFDA;
  cursor: pointer;
}

.certpage_archieve{
  margin-top: 15%;
  font-size: 600%;
}

.certpage_sideheading{
  font-weight: 500;
}

.cert_projecttabheading{
    font-weight: 600;
    font-size: 200%;
}

.cert_project_table{
    margin-top: 5%;
    padding-left: 3%;
}


@media only screen and (max-width: 600px) {
  .cert_projecttabheading{
    font-weight: 600;
    font-size: 100%;
    margin-left: 0%;
  }
  .certpage_archieve{
    margin-top: 15%;
    font-size: 300%;
    margin-left: 5%;  
  }
  .cert_project_table{
    margin-top: 5%;
    /* padding-left: 3%; */
  }
  .certpage_sideheading{
    font-weight: 500;
    margin-left: 3%;
  }
  .cert_projectname{
    font-size: 80%
  }
}

.cert_projectcol{
  font-size: 125%;
  color: #8892B0;
  margin-top: 2%;
}

.cert_projectname{
  color: #CCD6F6;
  font-weight: 700;
}

/* .cert_project_table:last-child{
  margin-bottom: 10%;
} */

.cert_projectcol:last-child{
  margin-bottom: 10%;
}

.cert_projectdesc{
  font-size: 70%;
}

.part_heading{
    margin-top: 8%;
}

.part_tabheading{
  font-weight: 600;
  font-size: 180%;
}

.part_project_table{
  margin-top: 4%;
}

.part_name{
  font-weight: 600;
}

.part_row{
  margin-top: 2%;
}

.part_row:last-child{
  margin-bottom: 10%;
}

.part_hr{
  margin-top: 2%;
}
